import React from "react";
import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import headerImg from "../assets/images/header-img.png";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';
// import { isVisible } from "@testing-library/user-event/dist/utils";

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(300 - Math.random() * 100);
//   const [index, setIndex] = useState(1);
  const toRotate = [ "Web Developer", "Web Designer", "UI/UX Designer" ];
  const period = 2000;

  useEffect(() => {

    let ticker = setInterval(() => {
        tick();
    }, delta);

    return () => { clearInterval(ticker) };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [text])

    const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setDelta(500);
    } 
  }

    return(
        <section className="banner" id="home">
        <Container>
            <Row className="align-items-center"> 
                <Col xs={12} md={6} xl={7}>
                  <TrackVisibility>
                  {({isVisible})=>
                    <div className={isVisible ?"animate__animated animate__fadeIn":""}>
                    <span className="tagline">Welcome to  my Portfoio</span>
                    <h1>{`Hi I'm Khushi `}<span className="wrap">{text}</span></h1>
                    <p>I am a web developer, as designing 
                    websites excites me and I have a natural sense of style. 
                    Aiming to create projects that would add value to the users.</p>
                    <button onClick={()=> console.log('connect')}>Let's Connect<ArrowRightCircle size={25}></ArrowRightCircle></button>
                    </div>}
                    </TrackVisibility>
                </Col>
                <Col xs={12} md={6} xl={5}>
                   <img src={headerImg} alt="Header-img"/>
                </Col>
            </Row>
        </Container>
        </section>
    );
}